.product-card-wrapper {
	background-color: var(--color-background-secondary);
	border-radius: 10px;
	overflow: hidden;
}

.product-card:hover {
	scale: 1.2;
	transition: ease-in-out 0.1s;
}

.product-card {
	position: relative;
	width: 100%;
	padding-top: 72.5%;
}

.product-card-inner {
	display: flex;
	flex-direction: column;
	position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.product-card-thumbnail {
	position: relative;
	width: 100%;
	padding-top: 56.25%;/* 16:9 */
}

.product-card-thumbnail > img {
	position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
	object-fit: cover;
}

.product-card-body {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-grow: 1;
}

.product-card-profile {
	position: absolute;
	width: 20%;
	height: auto;
	bottom: 5%;
	left: 5%;
	border: solid 1px white;
	border-radius: 30%;
}

.product-card-name {
	font-size: .5rem;
	margin-left: 25%;
}