header {
	display: flex;
	height: 100px;
	padding: 10px;
	gap: 0 20px;
	justify-content: space-between;
	align-items: center;
}

header > * {
	display: flex;
	height: 100%;
	gap: 0 15px;
}

.header-logo {
	height: 100%;
}

.header-box {
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	gap: 5px 0;
}

.header-box > * {
	flex: 1;
    height: calc(50% - 2.5px);
}

header ul {
	display: flex;
	gap: 0 15px;
}
header ul > li {
    display: flex;
    align-items: center;
    justify-content: center;
	padding: 1%;
}
header ul > li > a {
	height: 100%;
}

.header-search {
	display: flex;
    -webkit-box-align: center;
    align-items: center;
	background-color: var(--color-background);
	border: 1px solid var(--color-border);
	border-radius: 20px;
	height: 100%;
	padding: 0 15px 0 20px;
}

.header-search-input {
	width: 100%;
	height: 100%;
}

.header-search-icon {
	height: 75%;
	margin-left: 5px;
	cursor: pointer;
}

.header-theme {
	display: flex;
    align-items: center;
    border-radius: 15px;
    width: 65%;
    border: 1px solid var(--color-border);
	margin: 0 auto;
}

/* 버튼 만들어야함 */

.header-user-box {
	display: flex;
	height: 80%;
	background: var(--color-background-secondary);
	border-radius: 15px;/* 임시 */
	padding: 3%;
	justify-content: center;
    align-items: center;
	gap: 0 15px;
}

.header-user-profile {
	flex-wrap: wrap;
	height: 75%;
	border-radius: 50%;
}

.header-user-nickname {
	display: block;
	font-weight: bold;
	font-size: large;
	color: var(--color-secondary);
}
.header-user-point {
	display: block;
	color: #1a1c1e;
}
.header-user-logout {
	cursor: pointer;
}

