.walletMyProduct {
	margin-top: 50px;
	display: flex;
	flex-direction: column;
	/* align-items: center; */
	width: 1100px;
	background-color: rgb(226, 226, 229);
	border-radius: 30px;
	box-shadow: 10px 10px 5px rgb(191, 198, 209);
}

.myProductBoxs {
	display: flex;
	flex-wrap: wrap;
	padding: 50px 20px;
	gap: 20px;
}

.imgBoxAdmin {
	width: 245px;
	height: 200px;
	border-radius: 10px;
	background-color: rgb(226, 226, 229);
	box-shadow: 10px 10px 5px rgb(191, 198, 209);
}

.img1Admin {
	border-radius: 10px 10px 0 0;
	width: 100%;
	height: 80%;
}

.imgTitleAdmin {
	display: flex;
	justify-content: center;

}

.img2Admin {
	position: relative;
	width: 50px;
	height: 50px;
	bottom: 20px;
	border-radius: 10px;
	border: solid white;
}

.titleAdmin {
	margin-left: 5px;
	font-size: 16px;
	font-weight: 700;
}


.modal {
	margin-top: 20px;
	padding: 20px;
	/* background-color: #61dafb; */
	text-align: left;
}

.addBtn {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 245px;
	height: 200px;
	border: 0;
	cursor: pointer;
	background-color: rgb(226, 226, 229);
}

.addBtn:active {
	margin-left: 5px;
	margin-top: 5px;
	box-shadow: none;
}

.faPen {
	width: 55px;
	height: 55px;
	color: rgb(114, 120, 125);
}

.addProductName {
	font-size: 18px;
	font-weight: 700;
}