.login {
	position: relative;
	margin: auto;
	width: 70%;
	font-size: 18px;
	margin-bottom: 10vh;
}

.loginTitle {
	text-align: center;
	font-family: 'Helios Regular';
	font-style: normal;
	font-weight: 900;
	font-size: 60px;
	line-height: 70px;


	/* gray/900 */

	color: #2B2B2B;
}

.loginBox {
	background: #E3E6EC;
	box-shadow: 84.6413px 152.354px 189.032px rgba(209, 217, 230, 0.67), 84.6413px 67.7131px 95.9268px rgba(209, 217, 230, 0.34), 84.6413px 78.9986px 107.212px rgba(209, 217, 230, 0.4), 84.6413px 98.7482px 126.962px rgba(209, 217, 230, 0.48), -56.4275px -177.747px 183.39px rgba(255, 255, 255, 0.75), -56.4275px -103.923px 120.393px rgba(255, 255, 255, 0.539141), -56.4275px -88.489px 98.8215px rgba(255, 255, 255, 0.44708), -56.4275px -80.1792px 85.1116px rgba(255, 255, 255, 0.375);
	border-radius: 50px;
	padding: 15% 20%;
}


.checkedBox {
	font-size: 18px;
	font-weight: 550;
	color: #2B2B2B;
	margin: 32px 0 31px 0;
}

.blueButton {
	width: 100%;
	background-color: #406882;
	border-radius: 8px;
	border: none;
	cursor: pointer;

	font-weight: 500;
	font-size: 24px;
    padding: 18px 0;
	margin-bottom: 30px;

	color: #E4E3E3;
}

.lineBox {
	border-bottom: 1px solid #cecece;

	text-align: center;
	font-size: 2px;
	margin: 30px 0;
}

.iconBox {
	display: flex;
    justify-content: space-around;
}

.iconBox>img {
	width: 40px;
	height: 40px;
	cursor: pointer;
}

.inputBox {
	width: 100%;
	display: flex;
	flex-direction: column;
	font-family: 'Inter';
	font-style: normal;
	font-weight: 400;
	font-size: 24px;
	line-height: 29px;
	color: #828282;
	align-items: center;
}


.inputBox input {
	width: 100%;
	padding: 0 0.1rem;
	box-sizing: border-box;

	border-bottom: 2px solid #828282;
	border-top: none;
	border-left: none;
	border-right: none;

	background-color: transparent;
}

.inputBox *:not(:last-child) {
	margin-bottom: 30px;
}

.joinButton {
	border: none;
	outline: none;
	background-color: transparent;
	font-size: 2px;

	float: left;
}

.searAccButton {
	border: none;
	outline: none;
	background-color: transparent;
	font-size: 2px;

	float: right;
}

.searPassButton {
	border-top: none;
	border-right: none;
	border-left: solid 1px gray;
	border-bottom: none;

	outline: none;
	background-color: transparent;
	font-size: 2px;

	float: right;

}

.secondButtonBox > * {
	cursor: pointer;
}