@import "~react-image-gallery/styles/css/image-gallery.css";

.product-container {
	display: flex;
	flex-direction: column;
	padding: 5% 10%;
	gap: 50px 0;
	font-family: 'KoHo';
}

.product-container h1 {
    font-size: 4rem;
}

.product-container h2 {
	font-size: 2.5rem;
}

.product-container hr {
	height: 0;
	border: 2px solid;
	margin-bottom: 50px;
}

.product-container hr:not(:first-of-type) {
	margin-top: 50px;
}

.product-header {
	position: relative;
	width: 100%;
	padding-top: 56.25%;
	overflow: hidden;
}

.product-header-inner {
	position: absolute;
	top: 0;
	left: 0;
	display: flex;
	width: 100%;
	height: 100%;
	color: var(--color-background);
	border-radius: 18px;
	background: linear-gradient(to right, var(--color-tertiary), var(--color-secondary));
	padding: 2.5% 5% 5%;
	gap: 0 2%;
}

.product-header-left {
	flex: 6;
}

.product-header-right {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	flex: 3;
}

.product-box {
	display: flex;
    align-items: center;
    padding: 1rem;
    width: 100%;
    border-radius: 18px;
    background-color: var(--color-background-tertiary);
}

.product-header-title {
	color: var(--text-color);
	font-size: 2rem;
	font-weight: bold;
}

.product-header-desc {
	color: var(--text-color-secondary);
}

.product-header-time {
	align-self: end;
	color: var(--color-background)
}

.product-header-thumbnail {
	position: relative;
    width: 100%;
	padding-top: 56.25%;
    border-radius: 18px;
	overflow: hidden;
}
.product-header-thumbnail img {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.product-header-option-box {
	display: flex;
	align-items: center;
	justify-content: space-around;
	color: var(--color-background);
}

.product-header-option {
	display: flex;
	align-items: center;
	justify-content: space-between;
	white-space: pre-wrap;
}

.product-info {
	display: flex;
	gap: 10%;
}

.product-info > * {
	flex: 1 1 auto;
}

.product-info-box {
	display: flex;
	flex-direction: column;
    border-radius: 18px;
    border-style: solid;
	padding: 2rem;
}

.product-info-box * {
	white-space: nowrap;
	font-size: 1.5rem;
}

.product-info-box > *:not(:last-child)::after {
	content: '';
	display: block;
	height: 0;
	margin: .75rem 0;
	border-bottom: 1px solid;
}

.dropdown-menu {
	position: relative;
	height: 100%;
	display: inline-flex;
	flex-direction: column;
}

.dropdown-arrow {
	width: 1rem;
	float: right;
	cursor: pointer;
	transition: transform 300ms;
}
.dropdown-arrow.active {
	transform: rotate(180deg);
}

.product-category-box {
	display: flex;
	margin-top: 20px;
	gap: 0 10px;
}

.product-category {
	background-color: var(--color-tertiary);
	border-radius: 54px;
	color: var(--color-background);
	padding: 5px 20px;
}











