#myform fieldset {
  display: inline-block;
  direction: rtl;
  border: 0;
}
#myform fieldset legend {
  text-align: right;
}
#myform input[type="radio"] {
  display: none;
}
#myform label {
  font-size: 3em;
  color: transparent;
  text-shadow: 0 0 0 #f0f0f0;
  font-style: italic;
}
#myform label:hover {
  text-shadow: 0 0 0 rgba(15, 120, 67, 0.99);
  font-style: italic;
}
#myform label:hover ~ label {
  text-shadow: 0 0 0 rgba(15, 120, 67, 0.99);
  font-style: italic;
}
#myform input[type="radio"]:checked ~ label {
  text-shadow: 0 0 0 rgba(15, 120, 67, 0.99);
  font-style: italic;
}
