.myWallets{
    display: flex;
    flex-direction: column;
    margin-left: 100px;
    margin-top: 100px;
}

.myWalletTitle{
    width: 280px;
    display: flex;
    border-bottom: solid 3px rgb(64, 104, 130);
}

.walletImg{
    width: 65px;
    height: 60px;
    margin-bottom: 10px;
}

.walletName{
    margin-left: 30px;
    font-size: 45px;
    font-weight: 600;
    color: rgb(64, 104, 130);
}

.walletHomes{
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 1100px;
    height: 800px;
    background-color: rgb(226,226,229);
    border-radius: 30px;
    box-shadow: 10px 10px 5px rgb(191,198,209);
}

.wallet-status-icon {
	width: 10%;
	height: auto;
}

.warringImg{
    width: 110px;
    height: 110px;
}

.text1{
    margin-top: 80px;
    font-size: 40px;
    font-weight: 800;
}

.text2{
    font-size: 30px;
    font-weight: 800;
}

.walletRegister{
    width: 270px;
    height: 105px;
    border-radius: 25px;
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
      background: linear-gradient(
    rgb(183,204,246),
    rgb(132,166,230),
    rgb(183,204,246)
  );
}

.kaikasWallet{
    width: 40px;
    height: 38px;
}

.kaikasText{
    margin-top: 5px;
    font-size: 22px;
    font-weight: 700;
}

