#signup {
	display: flex;
	flex-direction: column;
	-webkit-box-flex: 1;
	-webkit-box-pack: start;
	flex-grow: 1;
	justify-content: center;
	/*flex-start;*/
	padding: 75px 24px;
	gap: 80px 0;
	width: 355px;
	margin: auto;
	align-items: center;
}

.signup-body {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 10px;
}

.signup-body > * {
	width: 100%;
}

.signup-label {
	font-weight: 700;
}

.signup-input {
	width: 100%;
	background: var(--color-background-secondary);
	border: .5px solid var(--color-border);
	box-shadow: -5px -5px 20px rgba(255, 255, 255, 0.53), 13px 14px 12px -6px rgba(166, 180, 200, 0.57);
	border-radius: 8px;
	padding: .75rem;
	margin: 10px 0 5px;
}

.signup-check-btn {
	float: right;
	color: var(--color-background-secondary);
	background-color: var(--color-secondary);
	border-radius: 5px;
	padding: .25rem;
}

.signup-desc {
	font-size: .5rem;
	font-weight: 400;
	float: right;
	color: var(--text-color-secondary);
}

.signup-submit {
	background-color: var(--color-secondary);
	box-shadow: inset 9px -9px 9px rgba(80, 116, 130, 0.1), inset -9px 9px 9px rgba(255, 255, 255, 0.1);
	backdrop-filter: blur(9px);
	border-radius: 24px;
	font-weight: 600;
	font-size: 1.5rem;
	line-height: 30px;
	color: var(--color-background-secondary);
	padding: 2rem 3rem;
}