* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
}



.item {
  display: flex;
  width: 500px;
  height: 275px;
  background: linear-gradient(rgb(109,153,170), rgb(195,213,221), rgb(109,153,170));
  border-radius: 20px;
  margin: auto;
}

.leftItem{
  display: flex;
  flex-direction: column;

}

.logo_icon {
  width: 50px;
  height: 50px;
  margin-top: 10px;
  margin-left: 10px;
}

.img_size {
  width: 154px;
  height: 96px;
  margin-top: 30px;
}

.likeBtn {
  display: flex;
  margin-top: 5px;
  margin-left: 20px;
}

.likeBtn_size {
  width: 12.53px;
  height: 11.95px;
  cursor: pointer;
}

.like_btn_color {
  border: 0;
  outline: 0;
  background-color: transparent;
  cursor: pointer;
}

.likeBtn_size:active {
  color: yellow;
}

.likeBtn_font {
  font-size: x-small;
  margin: 3px;
}

.evaluation {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-left: 10px;
}

.project_title{
  padding: 10px;
  border-bottom: 3px solid white;
}

.block, .leaf {
  display: flex;
  width: 300px;
  margin-top: 10px;
  background-color: white;
  border-radius: 10px;
  justify-content: space-around;
}

.blockImg {
  width: 45px;
}

/* .leaf {
  display: flex;
  margin-top: 10px;
  background-color: white;
  border-radius: 10px;
} */

.leafImg{
  width: 35px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.update{
  display: flex;
  justify-content: flex-end;
}

.items{
  margin-top: 40px;
}

.main_content{
  height: 500px;
  width: 100%;
  background: linear-gradient(rgb(109,153,170), rgb(195,213,221), rgb(109,153,170));
}

.main_content >.slick-slider{
  display: flex;
  align-items: center;
  justify-content: center;
}

.slick-list{
  width: 100%;
}

.main_content > .slick-slider > .slick-list{
  width: 100%;
}

.next_pageProductPage, .before_pageProductPage{
  border: 0;
  outline: 0;
  background-color: transparent;
  cursor: pointer;
}

#faRight, #faLeft{
  font-size: 85px;
  color: rgb(26,55,77);
}

#faRight{
  margin-right: 100px;
}
#faLeft{
  margin-left: 100px;
}