.sidebar {
  width: 235px;
  height: 1100px;
  background: linear-gradient(
    rgb(45, 92, 127),
    rgb(21, 43, 61),
    rgb(34, 73, 102)
  );
  border-radius: 0 30px 30px 0;
}

.logoBtn{
    background-color: transparent;
    margin-left: 10px;
}

.arrowLeft{
    width: 50px;
	height: auto;
    margin-top: 10px;
}

.myPage{
    margin-top: 15px;
    margin-bottom: 60px;
}

.myPage > *{
    color: white;
    border-left: solid 4px white;
}

.myPageTitle, .myPageKorean{
    padding-left: 10px;
    color: white;
}

.myPageKorean{
    padding-top: 10px;
}

.profile{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profileImg{
    width: 62px;
    height: 62px;
    border-radius: 50%;
}

.profileId{
    margin-top: 10px;
    color: white;
}

.profilePoint{
    color: gray;
}

.menuBar{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-top: 50px;
}

.menu{
    display: flex;
    align-items: center;
    margin-top: 30px;
    margin-left: 30px;
}

.menu:hover, .logOut{
    cursor: pointer;
}

#menuImg{
    width: 30px;
    height: 30px;
    color: rgb(247,247,247);
}

.menuName, .logOutName {
    color: white;
    margin-left: 15px;
    font-weight: 900;
}

#logOutImg, .logOutName{
    color: rgb(130,130,130);
}

#logOutImg:hover, .logOutName:hover{
    color: darkgray;
}

.logOut{
    margin-top: 100px;
    margin-left: 30px;
    display: flex;
    align-items: center;
}

.menuBtnName{
    background-color: transparent;
    border: 0;
}

.menuBtnName:hover{
    scale: 1.2;
    transition: ease-in-out 0.1s;
}

.linkLine{
    text-decoration-line: none;
}