.content-box {
	margin-bottom: 50px;
}

.content-box-header {
	display: flex;
	align-items: center;
	margin-bottom: 10px;
}

.content-box-body {
	position: relative;
	border-radius: 30px;
	background-color: #D9D9D9;
	padding: 40px 20px;
}

.content-box-icon {
	width: 4rem;
	height: 4rem;
	object-fit: contain;
	padding-right: 5px;
	margin-right: 5px;
	border-right: 2px solid var(--color-border);
}

.content-box-title {
	font-size: 1.5rem;
	font-weight: bold;
}

.content-box-icon ~ .content-box-title {
	font-family: 'Helios Regular';
	font-size: 2rem;
}

.content-box-more-btn {
	position: absolute;
	font-weight: 900;
	font-size: .75rem;
	bottom: 15px;
	right: 30px;
	background: transparent;
}








.imgBox:hover {
	scale: 1.2;
	transition: ease-in-out 0.1s;
}

.imgBox {
	width: 145px;
	height: 100px;
	margin-left: 20px;
	border-radius: 10px;
	background-color: rgb(226, 226, 229);
	box-shadow: 10px 10px 5px rgb(191, 198, 209);
}