.accountSetting {
  display: flex;
  flex-direction: column;
  margin-top: -20px;
}

.accountBox1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 180px;
  height: 200px;
  background-color: white;
  border-radius: 20px;
  margin-top: 80px;
  box-shadow: 10px 10px 5px rgb(191,198,209);
}

.accountText {
  font-size: 25px;
  font-weight: 700;
  margin-bottom: 20px;
}

.accountImg {
  width: 90px;
  height: 90px;
  border-radius: 50%;
}

.accountBox2 {
  display: flex;
  margin-right: 100px;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  border-radius: 20px;
  margin-top: 15px;
  width: 420px;
  box-shadow: 10px 10px 5px rgb(191,198,209);
}

.idText,
.nickName,
.pwText,
.myWalletText {
  font-size: 28px;
  font-weight: 600;
  margin-top: 25px;
  margin-bottom: 25px;
}

.nickName {
  margin-left: 20px;
}

.pwText {
  margin-left: 10px;
}

.idTextEmail {
  font-size: 20px;
}

.myWalletText {
  margin-left: -15px;
}

.accountBox3,
.accountBox4,
.accountBox5 {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  border-radius: 20px;
  margin-top: 15px;
  box-shadow: 10px 10px 5px rgb(191,198,209);
}

.accountBox3 {
  width: 340px;
}

.accountBox4 {
  width: 300px;
}

.accountBox5 {
  width: 725px;
}

.nickNameChangeBtn,
.pwChangeBtn {
  background-color: rgb(105, 152, 171);
  color: white;
  border: 0;
  padding: 10px;
  border-radius: 20px;
  cursor: pointer;
}

.nickNameChangeBtn:active,
.pwChangeBtn:active {
  margin-left: 1px;
  margin-top: 1px;
  box-shadow: none;
}
