@charset "utf-8";

* {
	box-sizing: border-box;
	letter-spacing: -.5px;
	word-break: keep-all;
	font-family: 'MonoplexKR', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	overflow-wrap: break-word;
	text-size-adjust: 100%;
	line-height: normal;
}

html, body, #root {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	color: var(--text-color);
	font-size: 16px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

li {
	/* float: left; */
	list-style: none;
}

a {
	text-decoration: none;
	cursor: pointer;
	color: inherit;
	font-size: inherit;
}

b {
    font-size: large;
}

input, textarea, select {
	border: none;
	outline: none;
}

textarea {
	resize: none;
}

button {
	border: none;
	outline: none;
	cursor: pointer;
	user-select: none;
}

input {
	font-size: inherit;
	border: none;
	outline: none;
}

input[type='submit'] {
	cursor: pointer;
	user-select: none;
}

img {
	height: 100%;
}

/*** input[type='search']의 x 표시 제거 ***/
/* IE10 이하 */
input::-ms-clear,
input::-ms-reveal {
	display: none;
}

/* Chrome */
input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
	display: none;
}